.popup-viewer {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: #22a7f0;
  display: none;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.popup-viewer-open {
  display: -ms-flexbox;
  display: flex;
}

.popup-close-button {
  border-radius: 50%;
