  top: 0.5rem;
  right: 0.5rem;
  width: 3rem;
  height: 3rem;
  background-color: rgba(6, 13, 45, 0.05);
  color: #060d2d;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
  font-size: 1.25rem;
}

.popup-close-button:hover {
  background-color: rgba(6, 13, 45, 0.15);
}

.popup-close-button:active {
  background-color: #22a7f0;
  color: white;
}


